.page {
  min-width: 320px;
}
.page__body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", serif, Times;
  font-weight: 400;
  font-style: normal;
  line-height: 24px;
  height: 100%;
  background-color: #ffffff;
  color: #283645;
}
@media (min-width: 1200px) {
  .page__body--wrapper {
    width: 1200px;
    min-width: none;
    margin: 0 auto;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 1200px) {
  .page__main {
    width: 936px;
    margin: 0 auto;
  }
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(../fonts/opensans.woff2), url(../fonts/opensans.woff);
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url(../fonts/opensansbold.woff2), url(../fonts/opensansbold.woff);
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url(../fonts/opensanslight.woff2), url(../fonts/opensanslight.woff);
}
.slider__toggle {
  width: 6px;
  height: 6px;
  border: 0;
  border-radius: 50%;
  background-color: #283645;
  opacity: 0.3;
  padding: 0;
}
.slider__toggle--current {
  outline: 3px solid #283645;
  background-color: #ffffff;
  opacity: 1;
}
.slider__toggle:hover:not(.slider__toggle--current) {
  opacity: 0.6;
  cursor: pointer;
}
.slider__toggle:active:not(.slider__toggle--current) {
  opacity: 0.1;
}
.slider__toggle:nth-child(2) {
  margin-left: 10px;
  margin-right: 10px;
}
.button {
  display: block;
  border: 3px solid #ffffff;
  border-radius: 29px;
  color: #ffffff;
  text-decoration: none;
  font-weight: 700;
  text-align: center;
  padding-top: 16px;
  background-color: transparent;
}
.button:hover {
  background-color: #ffffff;
  color: #d22856;
}
.button:active {
  color: rgba(210, 40, 86, 0.3);
}
.page-footer {
  background-color: #ffffff;
}
.contacts__wrapper {
  min-width: 300px;
  padding: 10px;
  position: relative;
}
@media (min-width: 1200px) {
  .contacts__wrapper {
    width: 1160px;
    padding: 0 20px;
    margin: 0 auto;
  }
}
.page-footer__map {
  background-color: #f2f2f2;
  background-image: url(../img/map.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 200px;
  position: relative;
}
@media (min-width: 660px) {
  .page-footer__map {
    height: 374px;
  }
}
@media (min-width: 1200px) {
  .page-footer__map {
    height: 496px;
  }
}
.page-footer__map .page-footer__frame {
  width: 100%;
  height: 100%;
}
.page-footer__map::before {
  content: "";
  position: absolute;
  background-image: url(../img/triangle.svg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  display: block;
  width: 100%;
  height: 35px;
}
@media (min-width: 660px) {
  .page-footer__map::before {
    height: 68px;
  }
}
@media (min-width: 1200px) {
