.page-header__toggle {
  width: 50px;
  height: 25px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: relative;
}
@media (min-width: 660px) {
  .page-header__toggle {
    margin-top: 4px;
  }
}
@media (min-width: 1200px) {
  .page-header__toggle {
    display: none;
  }
}
.page-header__toggle--closed::before {
  position: absolute;
  content: "";
  width: 50px;
  height: 3px;
  background-color: #ffffff;
  top: -2px;
  left: 0px;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.page-header__toggle--closed:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.page-header__toggle--closed:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
}
.page-header__toggle--opened::before,
.page-header__toggle--opened::after {
  position: absolute;
  content: "";
  width: 29px;
  height: 3px;
  background-color: #ffffff;
  right: -3px;
  top: 9px;
}
.page-header__toggle--opened::before {
  transform: rotate(45deg);
}
.page-header__toggle--opened::after {
  transform: rotate(-45deg);
}
.page-header__toggle--opened:hover::before,
.page-header__toggle--opened:hover::after {
  background-color: #d22856;
}
.page-header__toggle--opened:active::before,
.page-header__toggle--opened:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
